#sidenav {
  position: fixed;
  left: -250px;
  width: 250px;
  top: 64px;
  border-right: solid 1px black;
  bottom: 0;
  height: 100%;
  background: black;
  color: white;
  z-index: 2;
  padding: 20px;

  -webkit-transition: left 0.5s; /* Safari */
  -o-transition: left 0.5s;
  transition: left 0.5s;
}

@media screen and (max-width: 450px) {
  #sidenav {
    top: 56px;
  }
}

#sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

#sidenav a {
  display: block;
  color: white;
}

#sidenav.open {
  left: 0;
}

.open {
  font-size: 30px;
  cursor: pointer;
}



